@use "./modules/global.scss" as *;

.main {
  //Background
  background: rgb(0, 0, 0);
  background: radial-gradient(
    circle,
    rgba(0, 0, 0, 0.6) 70%,
    rgba(0, 0, 0, 0.8) 100%
  );
  background-attachment: fixed;

  //Background Image
  .image {
    background-image: url("/src/resources/images/background.jpg");
    background-attachment: fixed;
    background-position: center;
    background-size:cover;
    background-repeat: no-repeat;
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    z-index: -1;

    filter: blur(3px);
  }

  padding: map-get($map: $sizes, $key: "margin-1");

  .content {
    padding: map-get($map: $sizes, $key: "margin-1");
    padding-right: calc(map-get($map: $sizes, $key: "margin-1") * 2);
  }
}

.header {
  background-color: #fcf5da;
}
