@font-face {
  font-family: "Uertas Regular";
  src: url("/src/resources/fonts/UertasRegular.ttf");
}
@font-face {
  font-family: "BDO Grotesk";
  src: url("/src/resources/fonts/BDOGrotesk-VF.ttf");
}
@font-face {
  font-family: "Coupeur Jack Semibold";
  src: url("/src/resources/fonts/CoupeurJack-Semibold.otf");
}
.intro-text {
  border-left: 2px solid black;
}

.header {
  position: fixed;
  display: flex;
  flex-direction: row;
  left: 20px;
  top: 20px;
  border: 2px solid black;
}
.header a img {
  height: 32px;
  width: 32px;
  margin: 10px 15px;
}

.projects {
  display: flex;
  flex-direction: row;
  height: 80vh;
}
.projects .left {
  color: #1b1b1b;
  background-color: #fcf5da;
  display: flex;
  flex-direction: column;
  padding: 30px 5%;
  flex-basis: 25%;
}
.projects .left .top-title {
  flex-grow: 1;
}
.projects .left .top-title h2 {
  border-bottom: #1b1b1b solid 2px;
}
.projects .left .links {
  flex-basis: 10%;
}
.projects .right {
  color: #fcf5da;
  background-color: rgba(0, 0, 0, 0.5);
  flex-grow: 1;
  max-width: 66%;
}

.contact {
  display: flex;
  flex-direction: row;
  height: 100vh;
  justify-content: center;
  align-items: center;
  position: relative;
}
.contact .text {
  color: #fcf5da;
  background-color: rgba(0, 0, 0, 0.5);
  text-align: center;
  width: fit-content;
}
.contact .decorations .d3 {
  display: flex;
  flex-direction: row;
}
.contact .decorations .d4 {
  position: absolute;
  bottom: 30px;
  left: 30px;
}
.contact .decorations .d4 p {
  font-size: 18px;
}

.front {
  display: flex;
  flex-direction: row;
  flex-grow: 2;
  flex-basis: auto;
  height: 100vh;
  align-items: center;
}
.front .text * {
  width: fit-content;
}
.front .text h5 {
  position: relative;
  top: -1rem;
  borderbottom: solid 2px;
}
.front .decorations .d1 {
  display: flex;
  flex-direction: row;
}
.front .decorations .d2 {
  display: flex;
  flex-direction: row;
}

.who-am-i {
  display: flex;
  flex-direction: row;
  height: 80vh;
}
.who-am-i .left {
  color: #fcf5da;
  background-color: rgba(0, 0, 0, 0.5);
  padding: 30px 5%;
  flex-grow: 1;
}
.who-am-i .right {
  color: #1b1b1b;
  background-color: #fcf5da;
  display: flex;
  flex-direction: column;
  padding: 30px 5%;
  flex-basis: 25%;
}
.who-am-i .right h2 {
  border-bottom: #1b1b1b solid 2px;
}

