@font-face {
  font-family: "Uertas Regular";
  src: url("/src/resources/fonts/UertasRegular.ttf");
}
@font-face {
  font-family: "BDO Grotesk";
  src: url("/src/resources/fonts/BDOGrotesk-VF.ttf");
}
@font-face {
  font-family: "Coupeur Jack Semibold";
  src: url("/src/resources/fonts/CoupeurJack-Semibold.otf");
}
body {
  background-color: #fcf5da;
  color: #fcf5da;
  border-color: #fcf5da;
}

* {
  margin: 0;
  padding: 0;
}

h1 {
  font-family: "Uertas";
  font-size: 10.3rem;
}

h2 {
  font-family: "BDO Grotesk";
  font-size: 4.3rem;
  font-weight: 100;
  text-transform: uppercase;
  display: inline-block;
  border-bottom: #fcf5da solid 2px;
  margin-bottom: 30px;
}

h3 {
  font-family: "BDO Grotesk";
  font-size: min(3.5rem, 52px);
  font-weight: 900;
}

h4 {
  font-family: "BDO Grotesk";
  font-size: 2.5rem;
  font-weight: 900;
  text-transform: uppercase;
  display: inline-block;
}

h5 {
  font-family: "Coupeur Jack";
  font-size: 1.9rem;
}

p {
  font-family: "Consolas";
  font-size: min(1.5rem, 24px);
  display: inline;
}

a {
  color: inherit;
  text-decoration: none;
}
a:hover {
  font-weight: 600;
}

html {
  font-size: 1vw;
  overflow-y: scroll;
  scrollbar-width: none;
  -ms-overflow-style: none;
}
html::-webkit-scrollbar {
  width: 0;
  height: 0;
}

section {
  margin: 100px 0;
}

.s-light {
  background-color: #fcf5da;
  color: #1b1b1b;
}

.s-dark {
  background-color: rgba(0, 0, 0, 0.5);
  color: #fcf5da;
}

.border-dark {
  border-color: #1b1b1b;
}

.flex-row {
  display: flex;
  flex-direction: row;
}

@media (max-width: 400px) {
  .flex-row {
    flex-direction: column;
  }
}
.flex-col {
  display: flex;
  flex-direction: column;
}

.section {
  padding: 30px;
}

.text {
  padding: 30px 5%;
}

.decorations .d1 {
  position: absolute;
  bottom: 30px;
  left: 30px;
  align-items: center;
  gap: 30px;
  width: calc(100vw - 90px);
}
.decorations .d1 .line1 {
  width: 200px;
  height: 2px;
  background-color: #fcf5da;
}
.decorations .d1 .line2 {
  flex-grow: 1;
  height: 2px;
  background-color: #fcf5da;
}
.decorations .d2 {
  position: absolute;
  top: 30px;
  right: 30px;
  align-items: center;
  flex-direction: row-reverse;
  gap: 30px;
  width: calc(100vw - 460px);
}
.decorations .d2 .line1 {
  width: 200px;
  height: 2px;
  background-color: #fcf5da;
}
.decorations .d2 .line2 {
  flex-grow: 1;
  height: 2px;
  background-color: #fcf5da;
}
.decorations .d3 {
  position: absolute;
  bottom: 0;
  right: 0;
  align-items: center;
  flex-direction: row-reverse;
  gap: 30px;
  width: calc(100vw - 120px);
}
.decorations .d3 .line2 {
  width: 200px;
  height: 2px;
  background-color: #fcf5da;
}
.decorations .d3 .line1 {
  flex-grow: 1;
  height: 2px;
  background-color: #fcf5da;
}

