@use "modules/util.scss" as *;
@use "modules/colors.scss";
@use "modules/global" as *;

.intro-text {
  border-left: 2px solid black;
}

.header {
  position: fixed;
  @include flex(row);

  left: 20px;
  top: 20px;
  border: 2px solid black;

  a {
    img {
      height: 32px;
      width: 32px;
      margin: 10px 15px;
    }
  }
}
.projects {
  @include flex(row);

  height: 80vh;

  .left {
    @include section(light);
    @include flex(column);
    @include text();

    flex-basis: 25%;

    .top-title {
      flex-grow: 1;

      h2 {
        @include border("dark", "-bottom");
      }
    }

    .links {
      flex-basis: 10%;
    }
  }

  .right {
    @include section(dark);
    flex-grow: 1;
    max-width: 66%;
  }
}

.contact {
  @include flex(row);

  height: 100vh;
  justify-content: center;
  align-items: center;
  position: relative;

  .text {
    @include section("dark");

    text-align: center;
    width: fit-content;
  }

  .decorations {
    .d3 {
      @include flex(row);
    }
    .d4 {
      position: absolute;
      bottom: map-get($map: $sizes, $key: margin-1);
      left: map-get($map: $sizes, $key: margin-1);
      p {
        font-size: 18px;
      }
    }
  }
}

.front {
  @include flex(row);

  flex-grow: 2;
  flex-basis: auto;
  height: 100vh;
  align-items: center;

  .text {
    * {
      width: fit-content;
    }

    h5 {
      position: relative;
      top: -1rem;
      @include border(colors.$light, "bottom");
    }
  }
  .decorations {
    .d1 {
      @include flex(row);
    }
    .d2 {
      @include flex(row);
    }
  }
}

.who-am-i {
  @include flex(row);
  height: 80vh;
  .left {
    @include section(dark);
    @include text();

    flex-grow: 1;
  }

  .right {
    @include section(light);
    @include flex(column);
    @include text();
    flex-basis: 25%;

    h2 {
      @include border(dark, "-bottom");
    }
  }
}
