@use "./modules/global.scss" as *;
@use "./modules/colors.scss";

body {
  background-color: colors.$light;
  color: colors.$light;
  border-color: colors.$light;
}

* {
  margin: 0;
  padding: 0;
}

h1 {
  @include font-display();
}

h2 {
  @include font-h2();
  display: inline-block;
  border-bottom: map-get($map: $sizes, $key: "border");
  margin-bottom: map-get($map: $sizes, $key: "margin-1");
}

h3 {
  @include font-h3();
}

h4 {
  @include font-h4();
  display: inline-block;
}

h5 {
  @include font-decoration();
}

p {
  @include font-body();
  display: inline;
}

a {
  color: inherit;
  text-decoration: none;

  &:hover {
    font-weight: 600;
  }
}

html {

  font-size: 1vw;

  overflow-y: scroll;
  scrollbar-width: none;
  -ms-overflow-style: none;

  &::-webkit-scrollbar {
    width: 0;
    height: 0;
  }
}

section {
  margin: map-get($map: $sizes, $key: "margin-2") 0;
}

.s-light {
  background-color: colors.$light;
  color: colors.$dark;
}

.s-dark {
  background-color: colors.$transparent;
  color: colors.$light;
}

.border-dark {
  border-color: colors.$dark;
}

.flex-row {
  display: flex;
  flex-direction: row;
}

@media (max-width: 400px) {
  .flex-row {
    flex-direction: column;
  }
}

.flex-col {
  display: flex;
  flex-direction: column;
}

.section {
  padding: map-get($map: $sizes, $key: "margin-1");
}

.text {
  padding: map-get($map: $sizes, $key: "margin-1") 5%;
}

.decorations {
  .d1 {
    position: absolute;
    bottom: map-get($map: $sizes, $key: "margin-1");
    left: map-get($map: $sizes, $key: "margin-1");
    align-items: center;
    gap: map-get($map: $sizes, $key: "margin-1");
    width: calc(100vw - map-get($map: $sizes, $key: "margin-1") * 3);

    .line1 {
      width: 200px;
      height: 2px;
      background-color: colors.$light;
    }

    .line2 {
      flex-grow: 1;
      height: 2px;
      background-color: colors.$light;
    }
  }
  .d2 {
    position: absolute;
    top: map-get($map: $sizes, $key: "margin-1");
    right: map-get($map: $sizes, $key: "margin-1");
    align-items: center;
    flex-direction: row-reverse;
    gap: map-get($map: $sizes, $key: "margin-1");
    width: calc(100vw - (map-get($map: $sizes, $key: "margin-1") * 2 + 400px));

    .line1 {
      width: 200px;
      height: 2px;
      background-color: colors.$light;
    }

    .line2 {
      flex-grow: 1;
      height: 2px;
      background-color: colors.$light;
    }
  }
  .d3 {
    position: absolute;
    bottom: 0;
    right: 0;
    align-items: center;
    flex-direction: row-reverse;
    gap: map-get($map: $sizes, $key: "margin-1");
    width: calc(100vw - map-get($map: $sizes, $key: "margin-1") * 4);

    .line2 {
      width: 200px;
      height: 2px;
      background-color: colors.$light;
    }

    .line1 {
      flex-grow: 1;
      height: 2px;
      background-color: colors.$light;
    }
  }
}
